            { 'md-error': errors.has('zipCode') }
          ]"
                  >
                    <md-icon></md-icon>
                    <label class="fontFamily label">ZIP code</label>
                    <md-input
                      v-model="user.zipCode"
                      data-vv-name="zipCode"
