                      v-validate="{required:true}"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-size-50 md-small-size-100">
                  <md-field
                    :class="[
            { 'md-valid': !errors.has('country') && user.country },
            { 'md-form-group': true },
            { 'md-error': errors.has('country') }
          ]"
                  >
                    <md-icon></md-icon>
                    <label class="fontFamily label">Your country</label>
                    <md-input
                      v-model="user.country"
                      data-vv-name="country"
                      type="text"
                      name="country"
                      v-validate="{required:true}"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-size-50 md-small-size-100">
                  <md-field
                    :class="[
            { 'md-valid': !errors.has('referrer') && referrer },
            { 'md-form-group': true },
            { 'md-error': errors.has('referrer') }
          ]"
                  >
                    <md-icon></md-icon>
                    <label class="fontFamily label">Referral Email</label>
                    <md-input
                      v-model="referrer"
                      data-vv-name="referrer"
                      type="email"
                      name="referrer"
                      v-validate="{required:true}"
                      disabled
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-size-100 md-small-size-100">
                  <button class="mobbtn2" style="float:right;" @click="profileUpdate()">Save</button>
                </div>
              </div>
            </md-card-content>
          </md-card>
          <md-card>
            <md-card-header
              style=" background: #9bd3f9;border-bottom-left-radius:0px !important;border-bottom-right-radius:0px !important;"
              class="headerStyle"
            >
              <h3 class="fontFamily" style="font-weight:400;">KYC</h3>
            </md-card-header>

            <md-card-content>
              <div class="md-layout">
                <div class="md-layout-item md-size-45 md-small-size-100">
                  <div class="md-layout">
                    <div class="md-layout-item md-size-100 md-small-size-100">
                      <h5
                        class="fontFamily"
                        style="font-weight:600;margin-top:0px;"
                      >Proof of Identity</h5>
                      <p
                        class="fontFamily fontSize"
                      >&nbsp;&nbsp;Please upload the front and back sides of a proof of identity document.</p>
                      <p class="fontFamily fontSize">&nbsp;&nbsp;The uploaded document:</p>
                      <ul class="fontFamily fontSize">
                        <li>Should be a Government Issued ID (Driving Licence, Passport, Residence Permit etc.)</li>
                        <li>Should not be expired</li>
                        <li>Shows your date of birth</li>
                        <li>Should display your First and Last name which should be the same as the one maintained in your profile</li>
                        <li>Should be clearly readable with all corners perfectly visible</li>
                      </ul>
                    </div>
                    <div class="md-layout-item md-size-100 md-small-size-100">
                      <p class="fontFamily fontSize">Upload Document</p>
                      <p class="fontFamily fontSize" style="margin-bottom:0px;">Front side</p>
                      <button class="file_upload" type="button">
                        <span class="btn_lbl">Browse</span>
                        <span class="btn_colorlayer"></span>
                        <input type="file" name="fileupload" id="file_upload" />
                      </button>
                      <p class="fontFamily fontSize" style="margin-bottom:0px;">Back side</p>
                      <button class="file_upload" type="button">
                        <span class="btn_lbl">Browse</span>
                        <span class="btn_colorlayer"></span>
                        <input type="file" name="fileupload" id="file_upload" />
                      </button>
                    </div>
                    <div class="md-layout-item md-size-100 md-small-size-100">
                      <md-checkbox
                        class="check1"
                        v-model="user.proof"
                      >Document for both Identity and Proof of Residence</md-checkbox>
                    </div>
                    <div class="md-layout-item md-small-size-100 enableMbl">
                      <br>
                    </div>
                    <div
                      class="md-layout-item md-size-100 md-small-size-100"
                      style="text-align:center;"
                    >
                      <button class="mobbtn2">Upload</button>
                    </div>
                    <div class="md-layout-item md-small-size-100 enableMbl">
                      <br>
                    </div>
                  </div>
                </div>
                <div class="md-layout-item md-size-5">
                  <div class="vl"></div>
                </div>
                <div class="md-layout-item md-size-50 md-small-size-100">
                  <div class="md-layout">
                    <div class="md-layout-item md-size-100 md-small-size-100">
                      <h5
                        class="fontFamily"
                        style="font-weight:600;margin-top:0px;"
                      >Proof of Residence</h5>
                      <p
                        class="fontFamily fontSize"
                      >&nbsp;&nbsp;Please upload a proof of residence document.</p>
                      <p class="fontFamily fontSize">&nbsp;&nbsp;The uploaded document:</p>
                      <ul class="fontFamily fontSize">
                        <li>Should be a utility bill (electricity bill, telemobileNo bill etc.), a bank statement or a credit card statement</li>
                        <li>Should not be more than 3 months old</li>
                        <li>Should display your full name as mentioned in your user</li>
                        <li>Should display your full address as mentioned in your profile</li>
                        <li>Should be clearly readable with all corners perfectly visible</li>
                      </ul>
                    </div>
                    <div class="md-layout-item md-size-100 md-small-size-100">
                      <p class="fontFamily fontSize">Upload Document</p>
                      <button class="file_upload" type="button">
                        <span class="btn_lbl">Browse</span>
                        <span class="btn_colorlayer"></span>
                        <input type="file" name="fileupload" id="file_upload" />
                      </button>
                    </div> 
                    <div class="md-layout-item md-size-100 enableWeb">
                      <br />
                      <br />
                      <br />
                      <br />
                      <br />
                      <br />
                    </div>
                    <div
                      class="md-layout-item md-size-100 md-small-size-100"
                      style="text-align:center;"
                    >
                      
                      <button class="mobbtn2">Upload</button>
                    </div>
                  </div>
                </div>
              </div>
            </md-card-content>
          </md-card>
        </template>
        <template slot="tab-pane-2">
          <md-card>
            <md-card-header
              style=" background: #9bd3f9;border-bottom-left-radius:0px !important;border-bottom-right-radius:0px !important;"
              class="headerStyle"
            >
              <h3 class="fontFamily" style="font-weight:400;">Account Settings</h3>
            </md-card-header>
            <md-card-content>
              <div class="md-layout">
                <div class="md-layout-item md-size-50 md-small-size-100">
                  <md-field
                    :class="[
            { 'md-valid': !errors.has('email') && user.email },
            { 'md-form-group': true },
            { 'md-error': errors.has('email') }
          ]"
                  >
                    <md-icon></md-icon>
                    <label class="fontFamily label">Email</label>
                    <md-input
                      v-model="user.email"
                      data-vv-name="email"
                      type="email"
                      name="email"
                      v-validate="{required:true}"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-size-50 md-small-size-100">
                  <md-field
                    :class="[
            { 'md-valid': !errors.has('currentPassword') && user.currentPassword },
            { 'md-form-group': true },
            { 'md-error': errors.has('currentPassword') }
          ]"
                  >
                    <md-icon></md-icon>
                    <label class="fontFamily label">Current Password</label>
                    <md-input
                      v-model="user.currentPassword"
                      data-vv-name="currentPassword"
                      type="password"
                      name="currentPassword"
                      v-validate="{required:true}"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-size-50 md-small-size-100">
                  <md-field
                    :class="[
            { 'md-valid': !errors.has('password') && user.newpassword },
            { 'md-form-group': true },
            { 'md-error': errors.has('password') }
          ]"
                  >
                    <md-icon></md-icon>
                    <label class="fontFamily label">New password</label>
                    <md-input
                      v-model="user.newpassword"
                      data-vv-name="password"
                      type="password"
                      name="password"
                      v-validate="{required:true}"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-size-50 md-small-size-100">
                  <md-field
                    :class="[
            { 'md-valid': !errors.has('repeatPassword') && user.confirmPassword },
            { 'md-form-group': true },
            { 'md-error': errors.has('repeatPassword') }
          ]"
                  >
                    <md-icon></md-icon>
                    <label class="fontFamily label">Repeat password</label>
                    <md-input
                      v-model="user.confirmPassword"
                      data-vv-name="repeatPassword"
                      type="password"
                      name="repeatPassword"
                      v-validate="{required:true}"
                    ></md-input>
                  </md-field>
                </div>
                <div class="md-layout-item md-size-100 md-small-size-100">
                  <button class="mobbtn2" style="float:right;">Save</button>
                </div>
              </div>
            </md-card-content>
          </md-card>
        </template>
      </tabs>
    </div>
  </div>
</template>
<script>
import { Tabs } from "@/components";
import { Collapse } from "@/components";
import { APIService } from "../../apiservice.js";
import swal from "sweetalert2";
export default {
  components: {
    Tabs,
    Collapse,
  },
  data() {
    return {
      user: {
        firstName: "",
        lastName: "",
        email: "",
        mobileNo: "",
        address1: "",
        address2: "",
        city: "",
        state: "",
        zipCode: "",
        country: "",
        proof: false,
        currentPassword: "",
        newpassword: "",
        confirmPassword: "",
      },
        referrer: "",
    };
  },
  methods:{
    userInfo(){
      APIService.getUserInfo().then(
        res => {
          if(res.status == 200){
            this.user = res.data;
            this.user.currentPassword = "";
            if(this.user.referrer.accountType != 'Admin'){
              this.referrer = this.user.referrer.email;
            }
          }
        }
      )
    },
    profileUpdate(){
      APIService.updateUser(this.user).then(
        res => {
          if(res.status == 200){
            swal({
                title: "Success",
                text: "Your profile updated successfully.",
                type: "success",
                showConfirmButton: true,
                confirmButtonClass: "md-button md-success",
              })
          }
        }
      )
    }
  },
  created(){
    this.userInfo()
  }
};
</script>
<style>
@media (max-width: 992px) {
.vl {
  border-left: 0px solid grey;
  height:0px;
}
}
@media (min-width: 992px) {
  .vl {
  border-left: 2px solid grey;
  height: 600px;
}
}
.md-card-tabs
  .md-list
  .md-list-item
  .md-list-item-button
  .md-list-item-content {
  min-height: auto;
  padding: 10px 15px;
  justify-content: center;
  display: flex;
  color: #2875ff;
}
.md-card-tabs .active.md-list-item.md-info .md-list-item-button,
.md-card-tabs .active.md-list-item.md-info .md-list-item-button:hover,
.md-card-tabs .active.md-list-item.md-info .md-list-item-button:focus {
  background-color: white !important;
  /* box-shadow: 0 12px 20px -10px rgba(0, 188, 212, 0.28),
    0 4px 20px 0px rgba(0, 0, 0, 0.12), 0 7px 8px -5px rgba(0, 188, 212, 0.2); */
  border: 2px solid #2875ff;
}
.tab1Align > div:nth-child(2) > div > div:nth-child(2) > div:first-child {
  width: 100% !important;
}
.tab-pane-1 {
  width: 100% !important;
  height: 100% !important;
  overflow: hidden;
}
.tab-pane-2 {
  width: 100% !important;
  height: 100% !important;
  overflow: hidden;
}
.customerDesignAlign > div:first-child {
  display: none !important;
}
.customerDesignAlign > div:nth-child(2) {
  padding-top: 30px !important;
  background: #ffffff !important;
}
.customerDesignAlign > div:nth-child(2) > ul:first-child {
  background: #ffffff !important;
}
.check1 > .md-checkbox-label {
  font-family: AppleGothic;
  font-size: 16px;
}
</style>
<style scoped>
.mobbtn2 {
  color: #fff;
  font-family: AppleGothic;
  font-size: 20px;
  line-height: 46px;
  padding: 5px 10px;
  text-align: center;
  text-transform: uppercase;
  min-width: 160px;
  max-width: 100%;
  border: none;
  box-sizing: border-box;
  position: relative;
  z-index: 2;
  border-radius: 23px;
  background: #2875ff;
  cursor: pointer;
}
.fontSize {
  font-size: 16px;
}
.file_upload {
  position: relative;
  min-width: 90px;
  text-align: center;
  color: grey;
  line-height: 25px;
  background: #fff;
  border-radius: 2px solid grey;
  font-weight: 600;
}

a.file_upload {
  display: inline-block;
}

.file_upload .btn_lbl {
  position: relative;
  z-index: 2;
  pointer-events: none;
}

.file_upload .btn_colorlayer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 1;
  pointer-events: none;
}

.file_upload input[type="file"] {
  position: absolute;
  top: 3px;
  left: -86px;
  font-weight: 600;
  margin-left: 100%;
  color: grey;
  outline: none;
}
.headerStyle {
  border-radius: 25px !important;
}
.label {
  font-size: 15px !important;
  color: #000 !important;
}
</style>