import DashboardLayout from "@/pages/Dashboard/Layout/DashboardLayout.vue";
import AuthLayout from "@/pages/Dashboard/Pages/AuthLayout.vue";
import AuthLayout2 from "@/pages/Dashboard/Pages/AuthLayout2.vue";

//login
import signin from "@/pages/Dashboard/Login/signin.vue";
import signup from "@/pages/Dashboard/Login/signup.vue";
import forget from "@/pages/Dashboard/Login/forget.vue";
import reset from "@/pages/Dashboard/Login/reset.vue";
import referralsignup from "@/pages/Dashboard/Login/referralsignup.vue";
import buytoken from "@/pages/Dashboard/Login/buytoken.vue";

// Dashboard pages
import Dashboard from "@/pages/Dashboard/Dashboard.vue";
import profile from "@/pages/Dashboard/profile.vue";
import teamBuilding from "@/pages/Dashboard/teamBuilding/teamBuilding.vue";

let authPages = {
  path: "/",
  component: AuthLayout2,
  name: "Authentication",
  children: [
    {
      path: "login",
      name: "Login",
      components: { default: signin }
    },
    {
      path: "register",
      name: "Register",
      components: { default: signup }
    },
    {
      path: "forget",
      name: "forget",
      components: { default: forget }
    },
    {
      path: "reset",
      name: "reset",
      components: { default: reset }
    },
    {
      path: "referralsignup",
      name: "referralsignup",
      components: { default: referralsignup }
    },
    {
      path:"login/:token",
      name:"Login",
      component:signin
    },
    {
      path:"register/:ref",
      name: "Register",
      components: { default: signup }
    }
  ]
};


const routes = [
  {
    path: "/",
    redirect: "/login",
    name: "Login"
  },
  authPages,
  {
    path: "/",
    component: DashboardLayout,
    children: [
      {
        path: "dashboard",
        name: "Dashboard",
        components: { default: Dashboard }
      },
      {
        path:"profile",
        name:"Profile",
        components:{ default: profile}
      },
      {
        path:"teamBuilding",
        name:"Team Building",
        component:teamBuilding
      },
       {
      path: "buytoken",
      name: "buytoken",
      components: { default: buytoken }
    }
    ]
  }
];

export default routes;
