<script>
import { APIService } from "../../../apiservice.js";
export default {
  data() {
    return {
      user: {
        email: "",
        password: "",
      },
      errMsg: null,
      successMsg: null,
    };
  },
  methods: {
    signup() {
      this.$router.push({ name: "Register" });
    },
    forget() {
      this.$router.push({ name: "forget" });
    },
    onSubmit() {
      let self = this;
      this.errMsg = null;
      const { dispatch } = self.$store;
      APIService.login(this.user).then(
        (res) => {
          if (res.status == 200) {
            APIService.saveToken(res);
            self.$router.push({ name: "Dashboard" });
          } else {
            self.errMsg = res.message;
            self.user.password = "";
            this.errors.clear();
            this.$validator.reset();
            // this.$refs.pass.$el.focus();
          }
        },
        (err) => {
          self.errMsg = "Something went wrong. Please try after sometime.";
        }
      );
    },
  },
  created() {
    if (this.$route.params.token) {
      let data = {
        status: "Active",
      };
      APIService.updateUser(data).then((res) => {
        if (res.status == 200) {
          const { dispatch } = this.$store;
          dispatch("authentication/logout", "", { root: true });
          this.successMsg = "Email verification is successful";
        }
      });
    }
  },
};
</script>
