<script>
import { APIService } from "../../../apiservice.js";
import swal from "sweetalert2";
export default {
  data() {
    return {
      showInput: false,
      showParagraph: false,
      user: {
        firstName: "",
        lastName: "",
        email: "",
        password: "",
        confirmPassword: "",
        referrer: "",
        refType:"",
        parrentRefNo:""
      },
      errMsg: "",
      refBy: false,
    };
  },
  methods: {
    onSubmit() {
      let self = this;
      this.errMsg = "";
      if (
        this.user.firstName &&
        this.user.email &&
        this.user.password &&
        this.user.confirmPassword &&
        (!this.refBy || (this.user.referrer && this.refBy))
      ) {
        APIService.signUp(this.user).then(
          (res) => {
            if (res.status == 200) {
              swal({
                title: "Success",
                text: "Your registration is successfully.",
                type: "success",
                showConfirmButton: true,
                confirmButtonClass: "md-button md-success",
              }).then((result) => {
                if (result.value) {
                  this.$router.push({
                    path: "/login",
                  });
                }
              });
            } else {
              self.errMsg = res.message;
              self.user.password = "";
              this.errors.clear();
              this.$validator.reset();
              // this.$refs.pass.$el.focus();
            }
          },
          (err) => {
            self.errMsg = "Something went wrong. Please try after sometime.";
          }
        );
      } else {
        this.errMsg = "Please check all required fields";
      }
    },
    signin() {
      this.$router.push({ name: "Login" });
    },
  },
  created(){
    if(this.$route.params.ref){
      console.log('this.$route.params.ref',this.$route.params.ref);
      this.user.parrentRefNo = this.$route.params.ref;
    }
  }
};
</script>
