/** If enviroinment is production it take @param server_API else it will take @param local_API*/
// const API_URL = process.env.NODE_ENV == "production" ? server_API : local_API;
const API_URL = "https://unitedbackend.smartchain.in:8080/api/";
const ImageUrl = "https://triward.s3.ap-northeast-2.amazonaws.com/";
const ImageNativityUrl =
  "https://mustaqbal.smartchain.in:3000/static/Nativity_doc/";
const MustaqbalExplorer = "https://explorer.mustaqbalfund.net/tx/";
const TriwardExplorer = "https://explorer.triwards.com/tx/";
const MustaqbalExplorerToken = "https://explorer.mustaqbalfund.net/token/";
const EthereumExplorer = "https://rinkeby.etherscan.io/tx/";
const blockexplore = "http://adhichain.info/tx/";
const areaapi = "http://www.propertyfinder.ae/en/api/autocomplete/location";
const TRItoDoller = 1.2;
const WARDtoDoller = 1;
const gasUrl =
  "https://ethgasstation.info/api/ethgasAPI.json?api-key=ac925441c2513a1bc27ce0c9104d76c16366703c1d7967d20bb24ca2ef91";

import { API_ENDPOINT, ACCOUNT_TYPES } from "./utils/constants";
import axios from "axios";
import { authHeader } from "./_helpers";
export const APIService = {
  API_URL,
  ImageUrl,
  ImageNativityUrl,
  accountTypes: ACCOUNT_TYPES,
  MustaqbalExplorer,
  MustaqbalExplorerToken,
  EthereumExplorer,
  blockexplore,
  TRItoDoller,
  WARDtoDoller,
  TriwardExplorer,
  signUp(data) {
    const url = `${API_URL}${API_ENDPOINT.SIGN_UP}`;
    return axios.post(url, data).then(res => res.data);
  },
  login(data) {
    const url = `${API_URL}${API_ENDPOINT.LOGIN}`;
    return axios.post(url, data).then(res => res.data);
  },
  saveToken(data) {
    localStorage.setItem("user", JSON.stringify(data.data));
    localStorage.setItem("token", data.token);
  },
  getUserInfo() {
    const url = `${API_URL}${API_ENDPOINT.USER_INFO}`;
    const header = {
      headers: authHeader()
    };
    return axios.get(url, header).then(res => res.data);
  },
  sendVerificationMail(data){
    const url = `${API_URL}${API_ENDPOINT.SEND_VERIFICATION_MAIL}`;
    const header = {
      headers: authHeader()
    };
    return axios.post(url, data, header).then(res => res.data);
  },
  updateUser(data){
    const url = `${API_URL}${API_ENDPOINT.UPDATE_USER}`;
    const header = {
      headers: authHeader()
    };
    return axios.post(url, data, header).then(res => res.data);
  }
};
